<template>
  <div class="container">
    <h2>Tabela {{ name }}</h2>
    <div class="box">
      <input type="text" v-model="search" placeholder="Search title.." />
    </div>
    <div class="box">
      <button @click="changeEur">EUR</button>
      <button @click="changePln">PLN</button>
    </div>
    <table cellspacing="0" cellpadding="0">
      <tr>
        <th @click="sortList('name')">Nazwa ↓</th>
        <th @click="sortList('type')">Type ↓</th>
        <th @click="sortList('size')">Rozmiar ↓</th>
        <th @click="sortList('price')">Cena ↓</th>
      </tr>
      <tr v-for="(data, index) in filteredList" :key="index">
        <td>{{ data.name }}</td>
        <td>Na {{ data.type }}</td>
        <td>{{ data.size }}</td>
        <td v-if="euro == false">{{ data.price }}{{ walutaPln }}</td>
        <td v-else-if="euro == true">
          {{ data.price * 4.69 }}{{ walutaEuro }}
        </td>
      </tr>
      <tr>
        <td>
          <input type="text" placeholder="Nazwa" v-model="newName" />
        </td>
        <td>
          <select name="type" v-model="newType">
            <option value="drzewo">drzewo</option>
            <option value="ziemię" selected>ziemia</option>
            <option value="stolik">stolik</option>
          </select>
        </td>
        <td>
          <select name="size" placeholde="Rozmiar" v-model="newSize">
            <option value="Mały">Mały</option>
            <option value="Średni" selected>Średni</option>
            <option value="Duży">Duży</option>
          </select>
        </td>
        <td>
          <input
            class="inputPrice"
            type="number"
            placeholder="Cena"
            v-model="newPrice"
          />
        </td>
        <td><button @click="addItems">Dodaj rekordy</button></td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newName: "",
      newType: "",
      newSize: "",
      newPrice: Number,
      walutaPln: "zł",
      walutaEuro: "€",
      euro: false,
      items: [
        {
          name: "Domek",
          type: "ziemię",
          size: "Mała",
          price: 12,
        },
        {
          name: "Willa",
          type: "drzewo",
          size: "Duża",
          price: 24,
        },
        {
          name: "Karmnik",
          type: "stolik",
          size: "Średnia",
          price: 16,
        },
        {
          name: "Willa",
          type: "drzewo",
          size: "Duża",
          price: 24,
        },
      ],
      name: "Jasia",
      sortedData: [],
      sortedbyASC: true,
      search: "",
    };
  },

  mounted() {
    this.sortedData = this.items;
  },
  methods: {
    addItems() {
      if (this.newName && this.newType && this.newSize && this.newPrice != "") {
        this.items.push({
          name: this.newName,
          type: this.newType,
          size: this.newSize,
          price: this.newPrice,
        });
        this.newName = "";
        this.newType = "";
        this.newSize = "";
        this.newPrice = "";
      }
    },

    sortList(sortBy) {
      if (this.sortedbyASC) {
        this.sortedData.sort((x, y) => (x[sortBy] > y[sortBy] ? -1 : 1));
        this.sortedbyASC = false;
      } else {
        this.sortedData.sort((x, y) => (x[sortBy] < y[sortBy] ? -1 : 1));
        this.sortedbyASC = true;
      }
    },
    changeEur() {
      return (this.euro = true);
    },
    changePln() {
      return (this.euro = false);
    },
  },
  computed: {
    filteredList() {
      return this.items.filter((p) => {
        return p.name.toLowerCase().indexOf(this.search.toLowerCase()) != -1;
      });
    },
  },
};
</script>

